.card{
  line-height:1.5;
  font-family:"Raleway",sans-serif;
  background-color:#000;
  padding-bottom:9rem
}

.songs-wrapper{
  min-height:100vh;
  background-color:#000
}

.songs-wrapper .song{
  width:100%;
  overflow:hidden;
  position:relative;
  background-color:transparent;
  transition:all .2s ease-in
}

.songs-wrapper .song .song-image{
  width:100%;
  padding:50% 0;
  display:block;
  background-size:cover;
  background-position:center
}

.songs-wrapper .song .song-info{
  top:0;
  left:0;
  right:0;
  bottom:0;
  opacity:0;
  color:#fff;
  display:-webkit-flex;
  display:flex;
  position:absolute;
  -webkit-align-items:center;
  align-items:center;
  -webkit-flex-direction:column;
  flex-direction:column;
  -webkit-justify-content:center;
  justify-content:center;
  padding:10px;
  text-shadow:0 1px 3px #000;
  transition:opacity .2s ease-in;
  background:radial-gradient(circle at center, rgba(0, 0, 0, 0.4) 0, rgba(0, 0, 0, 0.9) 100%)
}

.songs-wrapper .song .song-info .song-title{
  font-weight:bold;
  font-size:20px;
  height:3rem;
  font-family:"Raleway",sans-serif
}

.songs-wrapper .song .song-info .song-title:hover{
  cursor:pointer
}

.songs-wrapper .song .song-info .song-artist{
  font-style:italic;
  font-size:15px
}

.songs-wrapper .song .song-info .song-artist:hover{
  cursor:pointer
}

.songs-wrapper .song:hover .song-info{
  opacity:1
}

.songs-wrapper .playing-song{
  border:solid 10px;
  border-image:url("data:image/svg+xml;charset=utf-8,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Cstyle%3Epath%7Banimation:stroke 5s infinite linear%3B%7D%40keyframes stroke%7Bto%7Bstroke-dashoffset:776%3B%7D%7D%3C/style%3E%3ClinearGradient id='g' x1='0%25' y1='0%25' x2='0%25' y2='100%25'%3E%3Cstop offset='0%25' stop-color='%232d3561' /%3E%3Cstop offset='25%25' stop-color='%23c05c7e' /%3E%3Cstop offset='50%25' stop-color='%23f3826f' /%3E%3Cstop offset='100%25' stop-color='%23ffb961' /%3E%3C/linearGradient%3E %3Cpath d='M1.5 1.5 l97 0l0 97l-97 0 l0 -97' stroke-linecap='square' stroke='url(%23g)' stroke-width='3' stroke-dasharray='388'/%3E %3C/svg%3E") 1
