.music-controller{
  background-color:rgba(41,41,41,.7);
  position:fixed;
  bottom:0px;
  height:9rem;
  display:block;
  width:100%
}

.music-controller .red-line{
  z-index:1;
  position:absolute;
  height:8px;
  background-color:red
}

.music-controller .ranger-slider{
  z-index:2;
  position:relative;
  width:100%;
  height:8px
}

.music-controller .slider{
  -webkit-appearance:none;
  top:0px;
  position:absolute;
  width:100%;
  height:8px;
  background:#d3d3d3;
  outline:none;
  opacity:.3;
  transition:opacity .2s
}

.music-controller .slider:hover{
  cursor:pointer
}

.music-controller .slider::-webkit-slider-thumb{
  -webkit-appearance:none;
  appearance:none;
  width:8px;
  height:8px;
  background:#d3d3d3;
  opacity:0;
  cursor:pointer
}

.music-controller .slider::-moz-range-thumb{
  width:8px;
  height:8px;
  background:#d3d3d3;
  opacity:0;
  cursor:pointer
}

.music-controller .timer{
  position:relative;
  color:#fff
}

.music-controller .timer .timer-current{
  position:absolute;
  top:10px;
  left:10px
}

.music-controller .timer .timer-length{
  position:absolute;
  top:10px;
  right:10px
}

.music-controller .timer .current-song{
  position:absolute;
  top:10px
}

.music-controller .play-controller{
  z-index:1;
  height:100%;
  width:100%;
  position:relative
}

.music-controller .play-controller .play-btn{
  margin-top:2.25rem;
  -webkit-align-items:center;
  align-items:center;
  color:#c9c9c9;
  opacity:.9;
  border-radius:40px
}

.music-controller .play-controller .play-btn:hover{
  opacity:1;
  color:#ccc;
  cursor:pointer
}

.music-controller .play-controller .play-btn:active{
  -webkit-transform:scale(0.9);
  transform:scale(0.9)
}

.music-controller .play-controller .skip-btn{
  color:#c9c9c9;
  margin-top:2.25rem
}

.music-controller .play-controller .skip-btn:hover{
  opacity:1;
  color:#ccc;
  cursor:pointer
}

.music-controller .play-controller .skip-btn:active{
  -webkit-transform:scale(0.9);
  transform:scale(0.9)
}

.music-controller .other-btn{
  z-index:2;
  position:absolute;
  height:2rem;
  width:100%;
  bottom:1rem
}

.music-controller .other-btn .random-btn{
  color:#c9c9c9;
  position:relative
}

.music-controller .other-btn .random-btn:hover{
  opacity:1;
  color:#ccc;
  cursor:pointer
}

.music-controller .other-btn .random-btn:active{
  -webkit-transform:scale(0.9);
  transform:scale(0.9)
}

.music-controller .other-btn .repeat-btn{
  color:#c9c9c9;
  position:relative
}

.music-controller .other-btn .repeat-btn:hover{
  opacity:1;
  color:#ccc;
  cursor:pointer
}

.music-controller .other-btn .repeat-btn:active{
  -webkit-transform:scale(0.9);
  transform:scale(0.9)
}
